export default {
	data() {
		return {
			form:{
				userName:'',
				password:''

			},
			passwordRepeat:''
		};
	},
	methods:{
		async submit(){
			 if (this.form.password !== this.passwordRepeat) {
				 uni.showToast({
				 	title: '登錄密碼不一致',
					icon: 'none'
				 });
				 return
			 }
			 uni.showLoading({
			 	title: '提交中',
				mask: true
			 })
			 const res = await this.$http.get('/user/sub_account',{data: this.form})
			 uni.hideLoading()
			 uni.showToast({
			 	title: '創建成功',
				duration: 1000
			 });
			 uni.navigateBack({
			 	delta: 1
			 });

		}
	}
}
